const RandomCatBackground = () => { return `cat${Math.floor(Math.random() * 4) + 1}` }

const RandomNumber = (lenght = 16) => {
    let card = '';
    do {
        card += Math.floor(Math.random() * 10)
    } while (card.length < lenght)

    return card
}

const newExpDate = () => {
    const month = new Date().getMonth() + 1
    const year = new Date().getFullYear() + 1

    return month + '/' + year
}

export const GenerateCard = ({ name, surname }) => ({
    number: RandomNumber(),
    name: name,
    surname: surname,
    expDate: newExpDate(),
    balance: 0,
    cvv: RandomNumber(3),
    background: RandomCatBackground(),
    history: [],
})