import './styles.scss';
import { Formik, Field, Form } from 'formik';
import { connect } from 'react-redux';

import { addMoney, closeForms } from 'store/actions';

const AddMoneyForm = ({ number, addMoney, closeForms }) => {

    return (<div className='custom-form-outlet'>
        <div className='shade-background'>
        </div>
        <div className="my-form">
            <div className="form-heading">
                <h4 className='form-title'></h4>
                <button className="close-button" onClick={closeForms}></button>
            </div>

            <Formik
                initialValues={{
                    amount: 0,
                }}

                onSubmit={async (values) => {
                    addMoney({ number, ...values });
                    closeForms();
                }}
            >
                <Form className='add-new-card-form'>
                    <label htmlFor="amount">'</label>
                    <Field
                        className="controls-input"
                        id="amount"
                        name="amount"
                        placeholder="0"
                        type = 'number'
                        required
                    />

                    <button type="submit" className='controls-button submit-button'></button>
                </Form>
            </Formik>
        </div>

    </div>)

}

const mapState = state => ({
    number: state.lastTargetCard
});

const mapDispatchToProps = {
    addMoney,
    closeForms
}

export default connect(mapState, mapDispatchToProps)(AddMoneyForm);