import './styles.scss';
import { Formik, Field, Form } from 'formik';
import { connect } from 'react-redux';

import { addCard, closeForms } from 'store/actions';

const NewCardForm = ({ addCard, closeForms }) => {

    return (<div className='custom-form-outlet'>
        <div className='shade-background'>
        </div>
        <div className="my-form">
            <div className="form-heading">
                <h4 className='form-title'> </h4>
                <button className="close-button" onClick={closeForms}></button>
            </div>

            <Formik
                initialValues={{
                    name: '',
                    surname: '',
                }}

                onSubmit={async (values) => {
                    console.log(values)
                    addCard(values);
                    closeForms();
                }}
            >
                <Form className='add-new-card-form'>
                    <label htmlFor="name">'</label>
                    <Field
                        className="controls-input"
                        id="name"
                        name="name"
                        placeholder=""
                    />

                    <label htmlFor="surname"></label>
                    <Field
                        className="controls-input"
                        id="surname"
                        name="surname"
                        placeholder=""
                    // required
                    />

                    <button type="submit" className='controls-button submit-button'>  </button>
                </Form>
            </Formik>
        </div>

    </div>)

}

const mapStateToPrors = state => ({

})

const mapDispatchToProps = {
    addCard,
    closeForms
}

export default connect(mapStateToPrors, mapDispatchToProps)(NewCardForm);