import './styles.scss';

import { connect } from 'react-redux';

import Card from './Card'
import AddCard from './AddCard'
import NewCardForm from './NewCardForm';
import AddMoneyForm from './AddMoney';
import CardHistory from './CardHistory';

import {
    showHistory,
    showAddCardForm,
    showAddMoneyForm
} from 'store/actions';


const Cards = ({ cards, addCardActive, addMoneyActive, cardHistoryActive }) => {

    let content;

    content = <>
        {
            cards.map((card) => <Card
                key={card.number}
                {...card}
            />)
        }
        {
            cards.length < 5 ?
                < AddCard />
                :
                ''
        }
        {
            addCardActive && <NewCardForm />
        }
        {
            addMoneyActive && <AddMoneyForm />
        }
        {
            cardHistoryActive && <CardHistory />
        }
    </>

    return (<>
        <div className="cards">
            {content}
        </div>
    </>
    )
}


const mapStateToPrors = state => ({
    cards: state.cards,
    addCardActive: state.forms.addCardActive,
    deleteCardActive: state.forms.deleteCardActive,
    addMoneyActive: state.forms.addMoneyActive,
    cardHistoryActive: state.forms.cardHistoryActive,
})

const mapDispatchToProps = {
    showHistory,
    showAddCardForm,
    showAddMoneyForm
}

export default connect(mapStateToPrors, mapDispatchToProps)(Cards);