import './styles.scss';
import { connect } from 'react-redux';
import { showAddMoneyForm, deleteCard, showHistory } from 'store/actions';


const Card = ({ number, name = 'alex', surname, expDate, balance, cvv, showAddMoneyForm, deleteCard, background, showHistory }) => (
    <div className={`card-item bank-card background-${background}`}>
        <div className="info-top">
            <div className="info-top">
                <div className="card-balance" onClick={() => showHistory(number)}>{balance + ""}</div>
                <button className="add-money card-button" onClick={() => showAddMoneyForm(number)}></button>
            </div>
            <button className="delete-card card-button" onClick={() => deleteCard(number)}></button>
        </div>
        <div className="info-bottom">
            <div className="cvv-container">
                <div className="cvv-pre">
                    <span className='cvv'>CVV</span>
                    <span className='cvc'>CVC</span>
                </div>
                <div className="cvv-num">
                    {cvv}
                </div>
            </div>

            <div className="card-number">
                {number}
            </div>

            <div className="exp-date-container">
                <div className="exp-pre">
                    <span className="exp-pre">VALID</span>
                    <span className="exp-pre">THROUGH</span>
                </div>
                <div className="exp-date">
                    {expDate}
                </div>
            </div>

            <div className="name">
                {name} {surname}
            </div>
        </div>
    </div>
)

const mapState = state => ({
    cards: state.cards
});

const mapFunc = {
    showAddMoneyForm,
    deleteCard,
    showHistory
}

export default connect(mapState, mapFunc)(Card);