import { ADD_CARD, DELETE_CARD, ADD_MONEY, DONATE_MONEY, SET_ACTIVE_TAB, SHOW_HISTORY, CLOSE_FORMS, SHOW_ADD_CARD, SHOW_ADD_MONEY } from './types';
import { GenerateCard } from './helpers';

const initialTodos = JSON.parse(localStorage.getItem('cards-app')) || {
    cards: [],
    activeTab: 'account',
    forms: {
        addCardActive: true,
        deleteCardActive: false,
        addMoneyActive: false,
        cardHistoryActive: false,
    },
    lastTargetCard: '',
};

const reducer = (state = initialTodos, action) => {

    switch (action.type) {

        case ADD_CARD: {
            const newCard = GenerateCard({ ...action.payload });
            const newCards = [...state.cards, newCard];

            return {
                ...state,
                cards: newCards,
            };
        }

        case DELETE_CARD: {
            const newState = { ...state };
            const target = action.payload;
            const newCards = newState.cards.filter(el => el.number != target);
            newState.lastTargetCard = action.payload;

            return {
                ...newState,
                cards: newCards,
            };
        }

        case ADD_MONEY: {
            const newState = { ...state };
            const target = newState.cards.findIndex(card => card.number == action.payload.number);
            const amount = action.payload.amount;
            const balance = parseInt(newState.cards[target].balance);
            const newBalance = (balance + parseInt(amount));

            newState.cards[target].balance = newBalance < 9999 ? newBalance : 9999;

            const balanceAction = { date: new Date().toLocaleDateString(), type: 'add', amount: amount, result: newState.cards[target].balance }

            newState.cards[target].history.push(balanceAction)

            return { ...newState }
        }

        case DONATE_MONEY: {
            const newState = { ...state };
            // const target = action.payload;
            // const newState = state.map(el => el.id == target ? { ...el, checked: !el.checked } : el);

            return { ...newState }
        }

        case SHOW_HISTORY: {
            const target = action.payload;
            const newState = { ...state };
            newState.forms.cardHistoryActive = true;
            newState.lastTargetCard = target;
            // const newState = state.map(el => el.id == target ? { ...el, checked: !el.checked } : el);

            return { ...newState }
        }

        case SHOW_ADD_CARD: {
            const newState = { ...state };
            newState.forms.addCardActive = true;

            return {
                ...newState,
            }
        }

        case SHOW_ADD_MONEY: {
            const newState = { ...state };
            newState.forms.addMoneyActive = true;
            newState.lastTargetCard = action.payload;

            return {
                ...newState,
            }
        }

        case CLOSE_FORMS: {

            return {
                ...state,
                forms: {
                    addCardActive: false,
                    deleteCardActive: false,
                    addMoneyActive: false,
                    cardHistoryActive: false,
                }
            }
        }

        case SET_ACTIVE_TAB: {
            const target = action.payload;
            return {
                ...state,
                activeTab: target
            }
        }

        default:
            return state
    }
}

export { reducer };